<template>
    <div id="homePage">
        <section class="section-content">
            <!-- 左侧主体开始 -->
            <project-module :projectData="projectData"></project-module>
            <!-- 左侧主体结束 -->
            <!-- 右侧主体开始 -->
            <div class="sidebar right-wrapper">
                <!-- 关于我 -->
                <about-module :basicInfo="basicInfo" :education="education"></about-module>
                <!-- 关于我 结束 -->
                <!-- 技能特长 -->
                <skill-module></skill-module>
                <!-- 技能特长结束 -->
                <!-- 自我评价 -->
                <assessment-module :assessmentData="assessmentData"></assessment-module>
                <!-- 自我评价结束 -->
                <label-module></label-module>
            </div>
            <!-- 右侧主体结束 -->
        </section>
    </div>
</template>
<script>
    import AboutModule from './About.vue'
    import SkillModule from './Skill.vue'
    import ProjectModule from './Project.vue'
    import LabelModule from './Label.vue'
    import AssessmentModule from './Assessment.vue'


// Assessment

    export default {
        data() {
            return {
                basicInfo:{},
                education:{},
                projectData:[],
                assessmentData:'',
                
            }
        },
        created(){
           this.getData()
           this.getData2()
           this.getProjectData()
           this.getAssessmentData()
        },
        methods:{
            getData(){
                this.$axios.get("/basicInfo.json").then(data=>{
                    return data.data
                }).then(data=>{
                    for(let key in data){
                        data[key].id=key;
                        this.basicInfo=data[key];
                    }
                }).catch(err=>{
                    
                })
            },
            getData2(){
                this.$axios.get("/education.json").then(data=>{
                    return data.data
                }).then(data=>{
                    for(let key in data){
                        data[key].id=key;
                        this.education=data[key];
                    }
                   
                }).catch(err=>{
                    
                })
            },
            getProjectData(){
                this.$axios.get("/workExperience.json").then(data=>{
                    return data.data
                }).then(data=>{
                    var projectDatas= [];
                    for(let key in data){
                        data[key].id=key;
                        projectDatas.push(data[key]);
                    }
                    this.projectData = projectDatas;
                }).catch(err=>{
                    
                })
            },
            getAssessmentData(){
                this.$axios.get("/selfAssessment.json").then(data=>{
                    return data.data
                }).then(data=>{
                    for(let key in data){
                        this.assessmentData = data[key]
                    }
                }).catch(err=>{
                    
                })
            }
            // education
        },
        components:{
            AboutModule,
            SkillModule,
            ProjectModule,
            LabelModule,
            AssessmentModule
        }
    }
</script>

<style>
    
</style>


