// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import VueI18n from 'vue-i18n'



axios.defaults.baseURL='https://wd2219169770cwmzem.wilddogio.com/'
axios.defaults.headers["Content-Type"] = 'application/x-www-form-urlencoded'        //配置请求header信息

Vue.config.productionTip = false
Vue.prototype.$axios = axios
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale:localStorage.getItem("lang")?localStorage.getItem("lang"):'zh-CN',    // 语言标识
  messages: {
    'zh-CN': require('./lang/cn'),   // 中文语言包
    'en-US': require('./lang/en')    // 英文语言包
  }
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  components: { App },
  template: '<App/>'
})



