<template>
    <div class="left-wrapper">
        <article class="content-item" v-for="(item, index) in newProjectData" :key="index">
            <div class="entry-media">
                <div class="post-ribbon" v-if="item.id == projectData[0].id">
                    <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 34 50"
                        enable-background="new 0 0 34 50" xml:space="preserve">
                        <g>
                            <polygon fill-rule="evenodd" clip-rule="evenodd" points="17,40.7 0.5,49.2 0.5,0.5 33.5,0.5 33.5,49.2" />
                            <path d="M33,1v47.4l-15.5-8L17,40.2l-0.5,0.2L1,48.4V1H33 M34,0H0v50l17-8.7L34,50V0L34,0z" />
                        </g>
                    </svg>
                    <i class="fa fa-star">最新</i>
                </div>
                <div class="post-title">
                    <h2><span>{{item.projectName}}</span></h2>
                    <div class="entry-date">
                        <ul>
                            <li>{{$t("m.exploit") + $t("m.environment")}}:{{item.environment}}</li>
                            <li>{{$t("m.exploit") + $t("m.time")}}:{{item.beginTime}} - {{item.finishTime}}</li>
                            <li>{{$t("m.exploit") + $t("m.frame")}}:{{item.framework}}</li>
                            <li>{{$t("m.exploit") + $t("m.tool")}}:{{item.tool}}</li>
                        </ul>
                    </div>
                </div>
                <div class="bubble-line">
                    <div class="left-shape">
                        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 15 30"
                            enable-background="new 0 0 15 30" xml:space="preserve">
                            <path fill-rule="evenodd" clip-rule="evenodd" fill="none" stroke="#000000" stroke-miterlimit="10" d="M0,29.4c0,0,7.5,0,7.5-7c0,0,7,0,7-7c0-0.1,0-0.1,0-0.2c0-0.1,0-0.1,0-0.2c0-7-7-7-7-7c0-7-7.5-7-7.5-7"></path>
                        </svg>
                    </div>
                    <div class="right-shape">
                        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 15 30"
                            enable-background="new 0 0 15 30" xml:space="preserve">
                            <path fill-rule="evenodd" clip-rule="evenodd" fill="none" stroke="#000000" stroke-miterlimit="10" d="M15,29.4c0,0-7.5,0-7.5-7c0,0-7,0-7-7c0-0.1,0-0.1,0-0.2c0-0.1,0-0.1,0-0.2c0-7,7-7,7-7c0-7,7.5-7,7.5-7"></path>
                        </svg>
                    </div>
                </div>
                <div class="post-content">
                    <p>
                        {{item.responsible}}
                    </p>
                </div>
                <div class="bubble-line">
                    <div class="left-shape">
                        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 15 30"
                            enable-background="new 0 0 15 30" xml:space="preserve">
                            <path fill-rule="evenodd" clip-rule="evenodd" fill="none" stroke="#000000" stroke-miterlimit="10" d="M0,29.4c0,0,7.5,0,7.5-7c0,0,7,0,7-7c0-0.1,0-0.1,0-0.2c0-0.1,0-0.1,0-0.2c0-7-7-7-7-7c0-7-7.5-7-7.5-7"></path>
                        </svg>
                    </div>
                    <div class="right-shape">
                        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 15 30"
                            enable-background="new 0 0 15 30" xml:space="preserve">
                            <path fill-rule="evenodd" clip-rule="evenodd" fill="none" stroke="#000000" stroke-miterlimit="10" d="M15,29.4c0,0-7.5,0-7.5-7c0,0-7,0-7-7c0-0.1,0-0.1,0-0.2c0-0.1,0-0.1,0-0.2c0-7,7-7,7-7c0-7,7.5-7,7.5-7"></path>
                        </svg>
                    </div>
                </div>

                <div class="post-footer">
                    <div class="post-footer-btn">
                        <span class="button" @click="toLink(item)">
                           {{$t("m.detail")}}
                        </span>
                    </div>
                </div>

            </div>
        </article>
        <div class="post-navigation">
            <ul>
                <li v-for="(item, index) in pageLeng" :key="index" @click="changeCurrentPage(index)">
                    <span :class="index+1 == currentPage?'active':''">{{index + 1}}</span>
                </li>
                <!-- <li><span>2</span></li>
                <li><span>3</span></li> -->

                <li><i class="fa fa-chevron-right"></i></li>
            </ul>
        </div>
    </div>
</template>

<script>
    let hyperlinkReg = /^http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- ./?%&=]*)?$/
    export default {
        props: {
            projectData: {
                type: Array,
                required: true
            }
        },
        data() {
            return {
                currentPage: 1,//当前页
                pageSize: 3      //每页个数
            }
        },
        computed: {
            newProjectData() {
                return this.projectData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
            },
            pageLeng() {
                return Math.ceil(this.projectData.length / this.pageSize)
            }
        },
        methods: {
            changeCurrentPage(index) {
                this.currentPage = index + 1;
            },
            toLink(item){
                console.log(item)
                if(hyperlinkReg.test(item.address)){
                    window.open(item.address)
                }else{
                    alert("该项目暂无地址")
                }
            }
        }
    }
</script>

<style>
</style>


