<template>
    <div class="widget">
        <h3 class="widget-title">
            <span>{{$t("m.about") + $t("m.me")}}</span>
        </h3>
        <div class="bubble-line">
            <div class="left-shape">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 15 30"
                    enable-background="new 0 0 15 30" xml:space="preserve">
                    <path fill-rule="evenodd" clip-rule="evenodd" fill="none" stroke="#000000" stroke-miterlimit="10" d="M0,29.4c0,0,7.5,0,7.5-7c0,0,7,0,7-7c0-0.1,0-0.1,0-0.2c0-0.1,0-0.1,0-0.2c0-7-7-7-7-7c0-7-7.5-7-7.5-7"></path>
                </svg>
            </div>
            <div class="right-shape">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 15 30"
                    enable-background="new 0 0 15 30" xml:space="preserve">
                    <path fill-rule="evenodd" clip-rule="evenodd" fill="none" stroke="#000000" stroke-miterlimit="10" d="M15,29.4c0,0-7.5,0-7.5-7c0,0-7,0-7-7c0-0.1,0-0.1,0-0.2c0-0.1,0-0.1,0-0.2c0-7,7-7,7-7c0-7,7.5-7,7.5-7"></path>
                </svg>
            </div>
        </div>

        <div class="widget-content">
            <!-- <img src="images/widget/about.jpg" alt="not image"> -->
            <p>{{$t("m.name")}}:{{basicInfo.name}}</p>
            <p>{{$t("m.gender")}}:{{basicInfo.gender}}</p>
            <p>{{$t("m.nation")}}:{{basicInfo.nation}}</p>
            <p>{{$t("m.schoolOfGraduation")}}:{{education.school}}</p>
            <p>{{$t("m.graduationDate")}}:{{education.graduate}}</p>
            <p>{{$t("m.specialty")}}:{{education.major}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            basicInfo: {
                type: Object,
                required: true
            },
            education: {
                type: Object,
                required: true
            }
        
        },
        data() {
            return {

            }
        },
    }
</script>

<style>
</style>


